  }),
   watch: {
      search (val) {
        let item = val.toUpperCase()
        if (item.length < 3) {
          this.loading = false     
        } else if (item.length > 3) {
          if (item.length == 3 && item in this.stocklist) {
            this.loading = true
            this.items = []
            this.select = null
            this.goToStockLookup(item)
          } else {
            this.loading = false
          }
        } else {
          this.loading = false          
        }
      },
    },
  
  created() {
    this.$store.dispatch('getTimeframes')
  },
  methods: {
    goToStockLookup(stock){
      this.$router.push({ name: 'Stock', params: { id: stock } })
    }, 
    goToDataPage(){
      this.$router.push({ name: 'Data'})
    },
    goToPortfolioPage() {
      this.$router.push({ name: 'Portfolio'})
    },
    goToPastPage(){
      this.$router.push({ name: 'Past'})
    },
    listAboveShort() {
