    }      
  },
  filters: {
    toCurrency(amount) {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      });
      return formatter.format(amount)
    },    
  },
  computed: {
    ...mapGetters(['timeframes', 'above_short', 'hour_data', 'daily_data', 'five_data']),

  },
}
