<script>
export default {
    props: ['stock', 'five_data', 'fifteen_data', 'thirty_data', 'hour_data'],
    data() {
        return {
            switch: true,
            toggle_one: 0,
            orderInfo: {
                stock: this.stock,
                quantity: 0,
                side: "",
                type: 'limit',
                time_in_force: 'day',
                limit_price: 0,
                stop_loss: 0
            }
        }
    },
    mounted() {
        this.quantity = this.getSizeAmount(this.five_data.each_frame.Price)
        this.limit_price = this.toCurrency(this.five_data.each_frame.Price)
        this.stop_loss = this.toCurrency(this.five_data.each_frame.SpanB_StopLoss)
    },
    methods: {
        fiveOrder: function() {
            if (this.five_data.title.includes('Above')) {
                this.orderInfo.side = 'buy'
            } else {
                this.orderInfo.side = 'sell'
            }
            this.orderInfo.limit_price = this.toCurrency(this.five_data.each_frame.Price)
            this.orderInfo.stop_loss = this.toCurrency(this.five_data.each_frame.SpanB_StopLoss)
            
        },
        fifteenOrder() {
            if (this.fifteen_data.title.includes('Above')) {
                this.orderInfo.side = 'buy'
            } else {
                this.orderInfo.side = 'sell'
            }
            this.orderInfo.limit_price = this.toCurrency(this.fifteen_data.each_frame.Price)
            this.orderInfo.stop_loss = this.toCurrency(this.fifteen_data.each_frame.SpanB_StopLoss)
        },
        thirtyOrder() {
            if (this.thirty_data.title.includes('Above')) {
                this.orderInfo.side = 'buy'
            } else {
                this.orderInfo.side = 'sell'
            }
            this.orderInfo.limit_price = this.toCurrency(this.thirty_data.each_frame.Price)
            this.orderInfo.stop_loss = this.toCurrency(this.thirty_data.each_frame.SpanB_StopLoss)
            
        },
        hourOrder() {
            if (this.hour_data.title.includes('Above')) {
                this.orderInfo.side = 'buy'
            } else {
                this.orderInfo.side = 'sell'
            }
            this.orderInfo.limit_price = this.toCurrency(this.hour_data.each_frame.Price)
            this.orderInfo.stop_loss = this.toCurrency(this.hour_data.each_frame.SpanB_StopLoss)

        },
        sendOrder() {
            // this.$store.dispatch('createOrder', this.orderInfo)
        },
        getSizeAmount(price) {
            var adj_price = Math.round((price + Number.EPSILON) * 100) / 100

            // console.log(adj_price);
            var size = Math.floor(1000 / adj_price);
            return size
        },
        tradeSide() {
            console.log(this.orderInfo.side)
            this.switch == !this.switch
            if (this.switch == true) {
                this.orderInfo.side == 'buy'
            } else {
                this.orderInfo.side == 'sell'
            }

        },
        toCurrency(amount) {
            var formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
            });
            return formatter.format(amount)
        }
      
    },
    filters: {
        toCurrency(amount) {
            var formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
            });
            return formatter.format(amount)
        }
    },
    
}
</script>

