    fifteen_data: state => {
      // create final array
      var frames = []
      // iterate through each stock in stocklist
      for (var k = 0; k < state.stocklist.length; k++) {
        // set stock variable
        var stock = state.stocklist[k]
        // set each timeframe in timeframes
        var result = Object.entries(state.timeframes)
        // iterate through each timeframe
        for (var i = 0; i < result.length; i++) {
          // set timeframe variable
          var arrayedObj = Object.keys(result[i][1])
          // iterate through each timeframe in timeframe
          for (var j = 0; j < arrayedObj.length; j++) {
            // if 15 in timeframe
            var title = result[i][0]
            // if stock in timeframe
            if (arrayedObj[j].includes(stock) && arrayedObj[j].length == stock.length && title.includes('15')) {

              // final overview array
              // var overview = []
              
              var each_frame = result[i][1][arrayedObj[j]]
              frames.push([title, each_frame])


            }
          }
        }
      }
      return frames
    },
